  .tabcontent__top-mob {
    display: flex;
  }
  .formloan__article-mob {
    display: block;
  }
  .formloan__article {
    display: none;
  }
  .formloan__btn {
    max-width: 343px;
  }
  .formloan__btn:after {
    display: block;
