  }
  .priceinfo__time:after {
    content: '';
    position: absolute;
    right: -40px;
    top: 50%;
    transform: translateY(-50%);
    width: 32px;
    height: 32px;
    background: url("../img/arrow-r.png") no-repeat center;
    opacity: 0.7;
  }
  .tabbuttons__img {
    display: block;
  }
  .tabbuttons__item {
    display: flex;
    justify-content: center;
  }
  .tabbuttons > li:nth-child(2) .tabbuttons__article,
  .tabbuttons > li:first-child .tabbuttons__article,
  .tabbuttons > li:nth-child(3) .tabbuttons__article {
    width: 58%;
  }
  .priceinfo {
    display: block;
  }
  .priceinfo__time {
    width: 100px;
    justify-content: center;
  }
  .tabcontent {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 5;
    border-radius: 10px 10px 0px 0px;
    padding-left: 16px;
    padding-right: 16px;
    display: none;
  }
  .tabcontent.wheat {
    background: #EBF3DE;
  }
  .tabcontent.wheat .tabcontent__top-mob li:nth-child(2) {
    color: black;
  }
  .tabcontent.wheat .formloan__sub-title,
  .tabcontent.wheat .formloan__article-mob {
    color: #233142;
  }
  .tabcontent.green {
    background: #28ABA8;
  }
  .formloan__list li {
    margin-bottom: 15px;
  }
  .formloan__list > li:last-child {
    padding-top: 5px;
  }
}

.footer__top {
  background: #1F6179;
  padding-top: 44px;
  padding-bottom: 153px;
